import React, { useState, useContext, createContext, Fragment } from "react";
import { Breadcrumb } from "@gull";
import { nanoid } from "nanoid";
import { Formik } from "formik";
import AppContext from "app/appContext";
import * as yup from "yup";
import { classList } from "@utils";
import CustomSelect from "../classrooms/CustomSelect";
import { generateTimeSlots } from "./functions/generateTimeSlots";
import { generateTime } from "./functions/generateTime";
import { Button, Spinner } from "react-bootstrap";
import { Link, useHistory } from "react-router-dom";
import { addClassroom } from "app/reducers/actions/ClassroomActions";
import Swal from "sweetalert2";
const basicFormSchema = yup.object().shape({
  roomName: yup.string().required("Room Name is required"),
  startTime: yup.string().required("Start Time is required"),
  endTime: yup.string().required("End Time is required"),
});
const AddClassroom = () => {
  const { classrooms, dispatch, user, token } = useContext(AppContext);
  const [loading, setLoading] = useState(false);
  const timeSlotOptions = generateTime(6, 21).map((slot) => {
    return {
      value: slot.value,
      label: slot.time,
    };
  });
  const history = useHistory();
  const handleSubmit = async (values) => {
    const { roomName, startTime, endTime } = values;
    const timeSlots = generateTimeSlots(startTime, endTime);
    const classroom = {
      id: nanoid(),
      roomName: roomName,
      timeSlots: timeSlots,
      created: Date.now(),
      user: user.email,
    };
    const notifications = {
      id: nanoid(),
      created: Date.now(),
      user: user.email,
      isViewed: false,
      action: "add",
      content: {
        name: roomName,
        location: "manage",
        description: "click to see more information",
      },
    };
    setLoading(true);
    await addClassroom(classroom, notifications, token)(dispatch);
    setLoading(false);
  };
  const handleCancel = async () => {
    Swal.fire({
      title: "Confirm to cancel",
      text: "Are you sure you want to cancel? If you cancel, all information that you have entered will be lost.",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes",
    }).then((result) => {
      if (result.isConfirmed) {
        history.push("/manage/list");
      }
    });
  };
  return (
    <Fragment>
      <Breadcrumb
        routeSegments={[{ name: "Home", path: "/" }, { name: "Add Classroom" }]}
      />
      {classrooms && (
        <div className="col-md-12">
          <div className="card">
            <div className="card-header">
              <strong>
                Please fill all the required (
                <span className="text-danger">*</span>) fields
              </strong>
            </div>
            <Formik
              initialValues={{
                roomName: "",
                startTime: "",
                endTime: "",
              }}
              validationSchema={basicFormSchema}
              onSubmit={handleSubmit}
            >
              {({
                values,
                errors,
                touched,
                handleChange,
                handleBlur,
                handleSubmit,
                isSubmitting,
                setFieldValue,
              }) => {
                return (
                  <form
                    className="needs-validation"
                    onSubmit={handleSubmit}
                    noValidate
                  >
                    {" "}
                    <div className="card-body">
                      <div className="form-row">
                        <div
                          className={classList({
                            "col-md-6 mb-3": true,
                            "valid-field": !errors.roomName && touched.roomName,
                            "invalid-field":
                              errors.roomName && touched.roomName,
                          })}
                        >
                          <label htmlFor="courseName">
                            Room Name (<span className="text-danger">*</span>)
                          </label>
                          <input
                            type="text"
                            className="form-control"
                            id="roomName"
                            placeholder="Room Name"
                            name="roomName"
                            value={values.roomName}
                            onChange={handleChange}
                            onBlur={handleBlur}
                            required
                          />
                          <div className="invalid-feedback">
                            {errors.roomName}
                          </div>
                        </div>
                      </div>
                      <div className="custom-separator"></div>
                      <div className="form-row">
                        <div
                          className={classList({
                            "col-md-6 mb-3": true,
                            "valid-field":
                              !errors.startTime && touched.startTime,
                            "invalid-field":
                              errors.startTime && touched.startTime,
                          })}
                        >
                          <label htmlFor="startTime">
                            Start Time (<span className="text-danger">*</span>)
                          </label>
                          <CustomSelect
                            name="startTime"
                            options={timeSlotOptions}
                            onChange={(value) =>
                              setFieldValue("startTime", value.value)
                            }
                            value={values.startTime}
                            required
                          />
                          <div className="invalid-feedback">
                            {errors.startTime}
                          </div>
                        </div>
                        <div
                          className={classList({
                            "col-md-6 mb-3": true,
                            "valid-field": !errors.endTime && touched.endTime,
                            "invalid-field": errors.endTime && touched.endTime,
                          })}
                        >
                          <label htmlFor="endTime">
                            End Time (<span className="text-danger">*</span>)
                          </label>
                          <CustomSelect
                            name="endTime"
                            options={timeSlotOptions}
                            onChange={(value) =>
                              setFieldValue("endTime", value.value)
                            }
                            value={values.endTime}
                            required
                          />
                          <div className="invalid-feedback">
                            {errors.endTime}
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className="card-footer">
                      <div className="mc-footer">
                        <Button
                          disabled={loading}
                          className="btn btn-primary mr-2"
                          type="submit"
                        >
                          Submit
                          {loading && (
                            <Spinner
                              size="sm"
                              variant="light"
                              className="mr-1"
                              animation="border"
                            />
                          )}
                        </Button>
                        <Button onClick={handleCancel} variant="danger">
                          Cancel
                        </Button>
                      </div>
                    </div>
                  </form>
                );
              }}
            </Formik>
          </div>
        </div>
      )}
    </Fragment>
  );
};

export default AddClassroom;
